@mixin white-gradient-left {
    background: linear-gradient(
      to right,
      rgba(51, 51, 51, 1) 0%,
      rgba(51, 51, 51, 0) 100%
    );
  }
  @mixin white-gradient-right {
    background: linear-gradient(
      to left,
      rgba(51, 51, 51, 1) 0%,
      rgba(51, 51, 51, 0) 100%
    );
  }
  
  html, body, #root{
    height: 100%;
    min-height: 100%;
    background: #333333;
  }
  
  body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }
  
  .body {
    overflow-y: scroll;
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
  }
  .body::-webkit-scrollbar { /* WebKit */
    width: 0;
    height: 0;
  }
  
  *{
    appearance: none;
  }

  .allMother {
    height: 100%;
    width: 100%;
    .allMain {
      display: flex;
      flex-direction: row;
      height: 100%;
      width: 100%;
  
      .leftSect {
        height: 100vh;
        width: calc(50% - 30px);
        background: #4698fe;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        padding-right: 30px;
        position: fixed;
  
        .headDiv {
          // margin-bottom: 100px;
          height: 156px;
          display: flex;
          justify-content: flex-end;
          width: 100%;
          .subHead {
            display: none;
          }
          .head {
            text-align: end;
            color: #333;
            font-size: 4rem;
  
            &:before {
              content: "Mobile ";
            }
          }
        }
      }
  
      .rightSect {
        height: 100%;
        width: 50%;
        background: #333;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position: relative;
        left: 50%;
        .rightSectChild {
          width: 100%;
  
          .clientItem{
              .clientEmail{
                  color: #ffffff;
                  margin: 5px 10px;
              }
          }
        }
      }
    }
  
    @media (max-width: 650px) {
      .allMain {
          flex-direction: column;
        .leftSect {
          // display: none;
  
              width: 100%;
              height: 180px;
              padding-right: 0;
              position: static;
              .headDiv {
                margin: 0;
                justify-content: center;
                align-items: flex-end;
                .head {
                  margin-top: 80px;
                  font-size: 3rem;
                  padding-bottom: 10px;
                }
              }
            
        }
        .rightSect {
          width: 100%;
          left: 0;
          top: 50px;
          .rightSectChild {
            display: flex;
            flex-direction: column;
            align-self: center;
            align-items: center;
            justify-content: flex-start;
          }
        }
      }
    }
  
    @media (max-width: 500px) {
      .allMain {
        .rightSect {
          .rightSectChild {
          }
        }
      }
    }
  
    // smaller phones
    @media (max-width: 400px) {
      .allMain {
        .rightSect {
          .rightSectChild {
          }
        }
      }
    }
  }
  